import { Row ,Col, Typography} from 'antd';
import React from 'react';
import CommonItems from 'Commonitem/CommonItem'
import SteelCard from './Steel';

const SteelComponent=()=>{
    return(
        <Row  style={{backgroundColor:'white',padding:'1rem',}} justify="center">
        <Row justify="center">
            <Typography.Title level={3}>
                Steel Items
            </Typography.Title>
           
        </Row>
        <Row gutter={[8,8]}>
            {
                CommonItems.map((item,index)=>(
                    <Col xxl={6} xl={6} lg={6} md={12} sm={24} xs={24} key={index}>
                        <SteelCard
                           name={item.name}
                           image={item.icon}
                           discount={item.discount}
                           price={item.price}
                           rate={item.rate}
                           address={item.address}
                        />
                    </Col>
                ))
            }
        </Row>
        </Row>
    )
}
export default SteelComponent;